
import axios from 'axios'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

export default {
  components: {
    Group,
    Selector,
    XInput,
    XButton,
    Checklist,
    Cell,
    Radio
  },
  data () {
    return {
      name: '',
      mobile: '',
      sum: '',
      workList: [ '公司上班', '个人经营企业主' ],
      workValue: '公司上班',
      assetList: [ '房', '车', '营业执照', '社保', '保单' ],
      chooseAsset: []
    }
  },
  methods: {
    chooseWorkValue (val) {
      this.workValue = val
    },
    submitApply () {
      if (!this.name) {
        alert('名称不能为空')
        return
      }
      if (!this.mobile) {
        alert('手机号码不能为空')
        return
      }
      axios.get('http://www.zxjlaravel.com/info/submitApply', { params: { type: 3, apply_type: this.workValue, name: this.name, mobile: this.mobile, sum: this.sum, assets: this.chooseAsset } })
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            location.href = '#/msg'
          } else {
            alert(data.errorInfo)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}



