
import axios from 'axios'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
  components: {
    Group,
    Selector,
    Card,
    XInput,
    XButton
  },
  data () {
    return {
      name: '',
      mobile: '',
      defaultIntro: {
        title: '8折买车险',
        detail: '8折买车险8折买车险8折买车险'
      }
    }
  },
  methods: {
    submitApply () {
      if (!this.name) {
        alert('名称不能为空')
        return
      }
      if (!this.mobile) {
        alert('手机号码不能为空')
        return
      }
      axios.get('http://www.zxjlaravel.com/info/submitApply', { params: { type: 4, name: this.name, mobile: this.mobile } })
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            location.href = '#/msg'
          } else {
            alert(data.errorInfo)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}



