
import { Swipe, SwipeItem } from 'vue-swipe'
import Masker from 'vux/src/components/masker/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'

import axios from 'axios'
var noticeIcon = require('../assets/notice.png')
var applyIcon = require('../assets/apply.png')
var unionIcon = require('../assets/union.png')
var newsIcon = require('../assets/news.png')
export default {
  components: {
    Swipe,
    SwipeItem,
    Masker,
    Marquee,
    MarqueeItem
  },
  data () {
    return {
      items: [],
      noticeIcon: noticeIcon,
      moduleIconList: [{
        title: '我要申请',
        img: applyIcon,
        link: '/applyList'
      }, {
        title: '我要加盟',
        img: unionIcon,
        link: '/Join'
      }, {
        title: '资讯',
        img: newsIcon,
        link: '/newsList'
      }],
      list: []
    }
  },
  methods: {
    getBanners () {
      var obj = this
      axios.get('http://www.zxjlaravel.com/home/banners')
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            obj.items = data.data
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    getNews () {
      var obj = this
      axios.get('http://www.zxjlaravel.com/home/newsList')
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            obj.list = data.data
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  },
  mounted: function () {
    this.getBanners()
    this.getNews()
  }
}



