
import axios from 'axios'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
  components: {
    Group,
    XInput,
    XButton,
    Checklist,
    Cell,
    Selector
  },
  data () {
    return {
      show: false,
      name: '',
      mobile: '',
      sum: '',
      joinList: [ '代理加盟', '我要兼职' ],
      joinValue: '代理加盟',
      list: [ '推荐客户贷款', '推荐客户装POS机', '推荐客户买车险' ],
      chooseType: []
    }
  },
  methods: {
    chooseJoinValue (val) {
      this.workValue = val
      if (val === '我要兼职') {
        this.show = true
      } else {
        this.show = false
      }
    },
    submitApply () {
      if (!this.name) {
        alert('名称不能为空')
        return
      }
      if (!this.mobile) {
        alert('手机号码不能为空')
        return
      }
      axios.get('http://www.zxjlaravel.com/info/submitApply', { params: { type: 4, name: this.name, mobile: this.mobile, apply_type: this.joinValue, assets: this.chooseType } })
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            location.href = '#/msg'
          } else {
            alert(data.errorInfo)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}



